export default function cartProduct(p) {
    return /* html*/ "\n    <li class=\"cart-list-item d-flex j-space-between\">\n        <div class=\"item-description d-flex grow a-items-center j-space-between\">\n            <p class=\"w-100 d-flex\">\n                " + p.name + "\n            </p>\n            <p>$" + p.price * p.qty + "</p>\n        </div>\n        <div class=\"cart-controls d-flex flex-col j-space-between\">\n        <span class=\"item-qty\">\n                <button class=\"btn js-add-1\" data-product-id=\"" + p.id + "\">+</button>\n                 " + p.qty + " \n                <button class=\"btn js-remove-1\" data-product-id=\"" + p.id + "\">-</button>\n                </span>\n            <button class=\"btn js-remove-from-cart btn-remove\" data-product-id=\"" + p.id + "\">\n                <i class=\"far fa-trash-alt\"></i>\n            </button>\n        </div>\n    </li>\n  ";
}
